<template lang="pug">
	div
		div(class="text-right mt-4")
			button(class="btn btn-primary" @click="openModal(true)") 

		table(class="table mt-4")
			thead
				tr
					th 
					th 
					th 
					th(class="text-right") 
					th(class="text-right") 
					th(class="text-right") 
					th(class="text-right") 
			tbody
				tr(v-for="(item, index) in products")
					td {{item.title}}
					td 
						img(:src="item.imageUrl" style="width: 100px; height: 100px" v-if="item.imageUrl")
					td {{item.category}}
					td(class="text-right") {{!item.origin_price ? 0 : item.origin_price | currency}}
					td(class="text-right") {{!item.price ? 0 : item.price | currency}}
					td(class="text-right")
						span(class="text-success" v-if="item.is_enabled" class="text-success") 
						span(class="text-danger" v-else) 
					td(class="text-right")
						button(class="btn btn-outline-primary btn-sm" @click="openModal(false, item)") 
						button(class="btn btn-outline-danger btn-sm" @click="openDelModal(item)") 

		Pagination(:pagination="pagination" @getInform="getProducts")

		<!-- Modal -->
		div(class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true")
			div(class="modal-dialog modal-lg" role="document")
				div(class="modal-content border-0")
					div(class="modal-header bg-dark text-white")
						h5(class="modal-title" id="exampleModalLabel")
							span(v-if="isNew === true") 
							span(v-else) 
						button(type="button" class="close" data-dismiss="modal" aria-label="Close")
							span(aria-hidden="true") &times;

					div(class="modal-body")
						div(class="row")
							div(class="col-sm-4")
								div(class="form-group")
									label(for="image") 
									input(type="text" class="form-control" id="image" v-model='tempProduct.imageUrl' placeholder='')
								div(class="form-group")
									label(for="customFile")  
									i(class="fas fa-spinner fa-spin")
									input(type="file" class="form-control" id="customFile" ref="files" @change="uploadFile")
								img(class="img-fluid" :src="tempProduct.imageUrl" alt="")

							div(class="col-sm-8")
								div(class="form-group")
									label(for="title") 
									input(type="text" class="form-control" id="title" v-model='tempProduct.title' placeholder="")

								div(class="form-row")
									div(class="form-group col-md-6")
										label(for="category") 
										select(v-model="tempProduct.category" class="form-control")
											option(v-for="(item, index) in GET_PRODUCTDATACLAS" v-if="item!==''" :value="item") {{item}} 
										
								div(class="form-row")
									div(class="form-group col-md-6")
										label(for="origin_price") 
										input(type="number" class="form-control" id="origin_price" v-model='tempProduct.origin_price' placeholder="")
									div(class="form-group col-md-6")
										label(for="price") 
										input(type="number" class="form-control" id="price" v-model='tempProduct.price' placeholder="")
								hr

								div(class="form-group")
									label(for="description") 
									textarea(type="text" class="form-control" id="description"  v-model='tempProduct.description' placeholder="")
								div(class="form-group")
									label(for="content") 
									textarea(type="text" class="form-control" id="content"  v-model='tempProduct.content' placeholder="")
								div(class="form-group")
									div(class="form-check")
										input(class="form-check-input" type="checkbox" id="is_enabled" v-model='tempProduct.is_enabled' :true:value='1' :false-value='0')
										label(class="form-check-label" for="is_enabled") 
						div(class="modal-footer")
							button(type="button" class="btn btn-outline-secondary" data-dismiss="modal") 
							button(type="button" class="btn btn-primary" @click='updateProduct()') 
		<!-- Modal -->
		div(class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true")
			div(class="modal-dialog" role="document")
				div(class="modal-content border-0")
					div(class="modal-header bg-danger text-white")
						h5(class="modal-title" id="exampleModalLabel")
							span 
						button(type="button" class="close" data-dismiss="modal" aria-label="Close")
							span(aria-hidden="true")
					div(class="modal-body")
						span 
						strong(class="text-danger") {{tempProduct.title}} ()
					div(class="modal-footer")
						button(type="button" class="btn btn-outline-secondary" data-dismiss="modal") 
						button(type="button" class="btn btn-danger" @click="deleteProduct()") 
</template>	

<script>
import $ from 'jquery'
import { mapGetters } from 'vuex'
import Pagination from '@/components/admin/pageNavigation'
export default {
	data() {
		return {
			page: 0,
			isNew: '',
			products: [],
			tempProduct: {},
			pagination: {}
		}
	},
	created() {
		this.getProducts()
	},
	computed: {
		...mapGetters([
			'GET_PRODUCTDATACLAS'
		]),
	},
	methods: {
		getProducts(page = 1) {
			this.page = page
			const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`
			this.$http.get(api).then((response) => {
				this.products = response.data.products
				this.pagination = response.data.pagination
				console.log(response.data.products)
			})
		},
		updateProduct() {
			let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`
			let httpMethod = 'post'

			if(!this.isNew) {	// 
				api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${this.tempProduct.id}`
				httpMethod = 'put'
			} 

			this.$http[httpMethod](api, {data: this.tempProduct}).then((response) => {
				if(response.data.success) {
					$('#productModal').modal('hide')
					this.getProducts()
				} else {
					alert('')
				}
			})
		},
		deleteProduct() {
			const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${this.tempProduct.id}`
			this.$http.delete(api).then((response) => {
					$('#delProductModal').modal('hide')
					this.getProducts()
			})
		},
		uploadFile() {
			const uploadedFile = this.$refs.files.files[0]
			const formData = new FormData()
			formData.append('file-to-upload', uploadedFile)
			
			const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`
			this.$http.post(api, formData, {
				headers: {'Content-Type': 'multipart/form-data'}
			}).then((response) => {
				if(response.data.success) {
					this.tempProduct.imageUrl = response.data.imageUrl
					this.$set(this.tempProduct, 'imageUrl', response.data.imageUrl)
				}
			})
		},
		openModal(isNew, item) {
			$('#productModal').modal('show')
			if(isNew) {
				this.tempProduct = {}
				this.isNew = true
			} else {
				this.tempProduct = Object.assign({}, item)
				this.isNew = false
			}
		},
		openDelModal(item) {
			$('#delProductModal').modal('show')
			this.tempProduct = Object.assign({}, item)
		}
	},
	components: {
        Pagination,
    }
}
</script>



